import React, { Component } from 'react';
import './about.css';
import {Grid, Row, Col} from 'react-bootstrap';
import foto from './img/ofi.jpg';
import foto1 from './img/black1.jpg';
import foto2 from './img/kv.png';
import logo from "./img/logi.png";
import linea from "./img/linea2.png";
import boot from "./img/bootstrap.png"
import javascript from "./img/javascript.png";
import jquery from "./img/jquery.png";
import react from "./img/react.png";
import contactKely from "./img/homee.png";
import hackhaton from "./img/premios1.png";
import cv from "./docs/Kely_Vargaya_CV.pdf";


const About = ({ }) => {
    return (
             <Grid className="descripcion" id="descripcion">

            <Col  lg={12} sm={12} md={12} className="cel visible-xs">              
                         
                 <Col xs={7} id="sobre_mi">
                    <h1>SOBRE MÍ</h1>
                      <p className="parrafito">Hola soy Kely, me encanta diseñar y desarrollar páginas web como aplicativos móviles. Por ello, me formé en Laboratoria, espacio en el que logré potenciar mis habilidades técnicas y blandas, que en conjunto me ayudan a alcanzar mis metas y aprovechar las oportunidades que presenta el mundo tecnológico.
                        Y estoy lista para empezar cualquier reto.</p>
                </Col>
                <Col xs={5}>
                    <img src={foto2} alt="Kely Vargaya" className="pequeño "/>
                </Col> 

            </Col>  
                <Col xsHidden lg={3} sm={1} md={1}>
                </Col>

                            <Col className="about"  lg={7} sm={7} md={7} >
                               <p className="parrafo hidden-xs">
                                 Hola soy Kely, me encanta diseñar y desarrollar páginas web como aplicativos móviles. Por ello, me formé en Laboratoria, espacio en el que logré potenciar mis habilidades técnicas y blandas, que en conjunto me ayudan a alcanzar mis metas y aprovechar las oportunidades que presenta el mundo tecnológico.
                                Y estoy lista para empezar cualquier reto.
                                </p>
                            
                            <Col className="blandas">
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-users" aria-hidden="true"></i>
                                    <h4>TRABAJO EN EQUIPO</h4>
                                </Col>  
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-assistive-listening-systems" aria-hidden="true"></i>
                                    <h4>ESCUCHA ACTIVA</h4>
                                </Col>
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-smile-o" aria-hidden="true"></i>
                                    <h4>TOLERANCIA AL ESTRES</h4>
                                </Col>
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-handshake-o" aria-hidden="true"></i>
                                    <h4>COMPROMISO</h4>
                                </Col>
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-universal-access" aria-hidden="true"></i>
                                    <h4>ADAPTABILIDAD</h4>
                                </Col>
                                <Col md={2} lg={2} xs={4} className="poder">
                                    <i class="fa fa-heartbeat" aria-hidden="true"></i>
                                    <h4>PASIÓN</h4>
                                </Col>

                            </Col>     
               
                                 <Col className="buttons">
                                     <div className="aa"> <a className="btnfos btnfos-2" href={cv} target="_blanck">Mi CV</a></div> 
                                 </Col>
                            </Col>
                        
                            <Col  lg={2} sm={2} md={2} className="portada hidden-xs">
                             <img src={foto2} alt="Kely Vargaya"/>                  
                           </Col>     
        </Grid>

    );

}

export default About;


