import React, { Component } from 'react';
import './sing_in.css';
import { Grid, Row, Col } from 'react-bootstrap';
import foto from './img/ofi.jpg';
import foto2 from './img/kv.png';
import logo from "./img/logi.png";
import linea from "./img/linea2.png";
import boot from "./img/bootstrap.png"
import javascript from "./img/javascript.png";
import jquery from "./img/jquery.png";
import react from "./img/react.png";
import contactKely from "./img/homee.png";
import hackhaton from "./img/premios1.png";

const Home = ({  }) => {
	return (    
		<div>
          <Grid id="home">

             <div id="mySidenav" className="sidenav">
              <a href="#home" id="about"><i className="fa fa-home" aria-hidden="true"></i></a>
              <a href="#descripcion" id="blog"><i className="fa fa-user-plus" aria-hidden="true"></i></a>
              <a href="#skills" id="habilidades"><i className="fa fa-graduation-cap" aria-hidden="true"></i></a>
              <a href="#portafolio" id="projects"><i className="fa fa-code" aria-hidden="true"></i></a>
              <a href="#contacto" id="contact"><i className="fa fa-phone" aria-hidden="true"></i></a>  
            </div>

            <div className="contenido">
                <div id="titulo">
                    <div className="h">
                      <h1>Kely Vargaya</h1>
                    </div>
                    <h3> Desarrolladora front end</h3>
                    <div className="subtitulo">
                      
                      <h4>Mi cultura se basa en la comprensión, <br/>la creatividad y la tecnología.</h4>
                    </div>
                    <div className="triangle"></div>
                </div>
             </div>
          </Grid>
    </div>
	);
}

export default Home;


